import React, { useCallback, useEffect, useRef, useState } from 'react';
import SideBar from './components/SideBar';
import Tracks from './components/Tracks';
import {
  Stage,
  StyledEditor,
  StyledEditorLeft,
  StyledEditorRight,
  StyledStageTimeline,
  StyledTimelineWrapper,
  TracksScrollContainer,
} from './styled';
import { EditorContext } from './type';
import { Clip } from '../common/types/clip';
import Ruler from './components/Ruler';
import { getTotalDuration, millisecondsToWidth } from './util';
import { useKeyboard } from './hooks/keyboard';
import { INIT_EDITOR_CONTEXT } from './config';
import { usePlayer } from './hooks/player';
import ConfigPanel from './components/ConfigPanel';
import Header from './components/Header';
import ToolBar from './components/ToolBar';
import ResizeRotate from './components/ResizeRotate';

export const editorContext = React.createContext<EditorContext>(INIT_EDITOR_CONTEXT);

const Editor: React.FC = () => {
  const [hoverTrack, setHoverTrack] = useState<HTMLElement | null>(null);
  const [tracks, setTracks] = useState<Clip[][]>([[]]);
  const [newTrackIndex, setNewTrackIndex] = useState(-1);
  const [totalDuration, setTotalDuration] = useState(0);
  const [activeClipId, setActiveClipId] = useState('');
  const [currentTime, setCurrentTime] = useState(0);
  const [isPlaying, setIsPlaying] = useState(false);
  const [draggingClip, setDraggingClip] = useState<Clip>();
  const playerContainerRef = useRef<HTMLDivElement>(null);

  const handleTracksChange = useCallback((tracks: Clip[][]) => {
    const newTracks = tracks.filter((track) => track.length);
    const newTotalDuration = getTotalDuration(newTracks);
    if (newTotalDuration !== totalDuration) {
      setTotalDuration(newTotalDuration);
      if (currentTime > newTotalDuration) {
        setCurrentTime(newTotalDuration);
      }
    }
    // zIndex
    newTracks.flat(1).forEach((clip, index) => {
      clip.zIndex = index;
    });
    setTracks(newTracks.length ? newTracks : [[]]);
  }, [currentTime, setCurrentTime, totalDuration]);

  useEffect(() => {
    // 
    if (totalDuration) {
      const scrollContainer = document.querySelector('.tracks-wrapper') as HTMLElement;
      if (scrollContainer) {
        const currentWidth = scrollContainer.clientWidth;
        const newWidth = millisecondsToWidth(totalDuration);
        if (currentWidth < newWidth) {
          scrollContainer.style.width = `${newWidth + 100}px`;
        }
      }
    }
  }, [totalDuration]);

  const editorContextValue = {
    hoverTrack,
    setHoverTrack,
    tracks,
    setTracks: handleTracksChange,
    newTrackIndex,
    setNewTrackIndex,
    activeClipId,
    setActiveClipId,
    currentTime,
    setCurrentTime,
    totalDuration,
    setTotalDuration,
    isPlaying,
    setIsPlaying,
    draggingClip,
    setDraggingClip,
  };

  const playerContext = usePlayer(editorContextValue, playerContainerRef);

  // 
  useKeyboard(editorContextValue, playerContext);

  const handleDocumentClick = useCallback(() => {
    if (activeClipId) {
      setActiveClipId('');
    }
  }, [activeClipId]);

  useEffect(() => {
    document.removeEventListener('click', handleDocumentClick, false);
    document.addEventListener('click', handleDocumentClick, false);
    return () => {
      document.removeEventListener('click', handleDocumentClick, false);
    };
  }, [handleDocumentClick]);

  return (
    <editorContext.Provider value={editorContextValue}>
      <Header player={playerContext.player} />
      <ResizeRotate />
      <StyledEditor>
        <StyledEditorLeft>
          <SideBar />
        </StyledEditorLeft>
        <StyledEditorRight>
          <StyledStageTimeline>
            <Stage>
              <div className="player-container" ref={playerContainerRef} style={{ width: 768, height: 432 }}></div>
              <ConfigPanel />
            </Stage>
            <StyledTimelineWrapper>
              <ToolBar playerContext={playerContext} />
              <TracksScrollContainer>
                <Ruler />
                <Tracks onCurrentTimeChange={playerContext.handleCurrentTimeChange} />
              </TracksScrollContainer>
            </StyledTimelineWrapper>
          </StyledStageTimeline>
        </StyledEditorRight>
      </StyledEditor>
    </editorContext.Provider>
  );
};

export default Editor;
