import React, { useEffect, useRef } from 'react';
import { ImageClip } from '../../../../../../../common/types/clip';
import { TRACK_HEIGHT } from '../../../../../../config';
import { millisecondsToWidth } from '../../../../../../util';

type Props = {
  clip: ImageClip;
};

async function drawImageToCanvas(
  canvas: HTMLCanvasElement,
  imageUrl: string,
  snapWidth: number,
  snapHeight: number,
  snapCount: number,
) {
  const ctx = canvas.getContext('2d');
  const image = new Image();
  image.onload = function() {
    let index = 0;
    while (index < snapCount) {
      ctx?.drawImage(image, snapWidth * index, 0, snapWidth, snapHeight);
      index += 1;
    }
  };
  image.src = imageUrl;
}

const ImageTrack: React.FC<Props> = ({ clip }) => {
  const { positionInTimeline, originalWidth, originalHeight } = clip;
  const canvasRef = useRef<HTMLCanvasElement>(null);
  useEffect(() => {
    const canvas = canvasRef.current;
    if (canvas) {
      const { width, height: snapHeight } = canvas.getBoundingClientRect();
      const snapWidth = snapHeight * originalWidth / originalHeight;
      const snapCount = Math.ceil(width / snapWidth); // snap
      drawImageToCanvas(canvas, clip.blobUrl, snapWidth, snapHeight, snapCount);
    }
  }, [clip.blobUrl, originalHeight, originalWidth, positionInTimeline.duration]);
  return (
    <div style={{ width: '100%' }}>
      <div style={{ position: 'absolute', left: 0, right: 0, top: 0, bottom: 0 }}>
        <canvas
          ref={canvasRef}
          width={millisecondsToWidth(positionInTimeline.duration)}
          height={TRACK_HEIGHT}
        />
      </div>
    </div>
  );
};

export default ImageTrack;
