import { TextClip } from '../types/clip';

export function getPixelRatio() {
  return 2;
}

export function setStyles(el: HTMLElement, styles: Partial<CSSStyleDeclaration>) {
  Object.keys(styles).forEach((key) => {
    // @ts-ignore
    el.style.setProperty(key.replace(/([A-Z])/g, '-$1').toLowerCase(), styles[key as keyof CSSStyleDeclaration]);
  });
}

export function formatMillisecond(ms: number, showMS: boolean = false, showHour: boolean = true) {
  let result = '';
  const msRemainder = ms % 1000;
  if (msRemainder || showMS) {
    result = `.${String(msRemainder).padStart(3, '0')}`
  }

  const seconds = (ms - msRemainder) / 1000;
  const secondRemainder = seconds % 60;
  result = `${String(secondRemainder).padStart(2, '0')}${result}`;

  const minutes = (seconds - secondRemainder) / 60;
  const minuteRemainder = minutes % 60;
  result = `${String(minuteRemainder).padStart(2, '0')}:${result}`;

  const hours = (minutes - minuteRemainder) / 60;
  if (showHour || hours) {
    result = `${String(hours).padStart(2, '0')}:${result}`;
  }
  return result;
}

export function svgToCanvas(svg: string, canvas: HTMLCanvasElement, canvasContext: CanvasRenderingContext2D | null | undefined) {
  return new Promise((resolve, reject) => {
    const { width, height } = canvas;
    const image = new Image();
    image.width = width;
    image.height = height;
    image.onload = function() {
      if (canvasContext) {
        canvasContext.clearRect(0, 0, width, height);
        canvasContext.drawImage(image, 0, 0);
      }
      resolve(true);
    };
    image.onerror = function(error) {
      reject(error);
    };
    image.src = `data:image/svg+xml;base64,${btoa(unescape(encodeURIComponent(svg.trim())))}`;
  });
}

export function sleep(ms: number) {
  return new Promise((resolve) => {
    window.setTimeout(resolve, ms);
  });
}

export function downloadFile(fileURL: string, fileName: string) {
  const saveLink = document.createElement('a');
  saveLink.href = fileURL;
  saveLink.target = '_blank';
  saveLink.download = fileName;
  if (navigator.userAgent.toLowerCase().match(/(ipad|iphone|safari)/) && navigator.userAgent.search('Chrome') < 0) {
    document.location = saveLink.href;
  } else { // window event not working here
    const event = new MouseEvent('click', {
      view: window,
      bubbles: true,
      cancelable: false,
    });
    saveLink.dispatchEvent(event);
    (window.URL || window.webkitURL).revokeObjectURL(saveLink.href);
  }
}

export function getTextClipCssText(clip: TextClip) {
  const { fontColor, fontSize, fontFamily } = clip.props;
  return [
    `color:${fontColor}`,
    `font-size:${fontSize}px`,
    `font-family:${fontFamily}`,
    'font-weight:200',
    'display:inline-block',
    'transform-origin:left top',
    `transform:scale(${getPixelRatio()})`,
  ].join(';');
}

export function getTextClipSize(clip: TextClip) {
  let container = document.querySelector('.div-for-text-size-calc') as HTMLDivElement;
  if (!container) {
    container = document.createElement('div');
    container.setAttribute('class', 'div-for-text-size-calc');
    document.body.appendChild(container);
  }
  container.innerText = clip.value;
  container.style.cssText = getTextClipCssText(clip);
  const bound = container.getBoundingClientRect();
  return {
    width: Math.ceil(bound.width),
    height: Math.ceil(bound.height),
  };
}

export function textClipToSvg(clip: TextClip) {
  return `<svg xmlns="http://www.w3.org/2000/svg" width="100%" height="100%">
    <foreignObject width="100%" height="100%">
      <div xmlns="http://www.w3.org/1999/xhtml">
        <div style="${getTextClipCssText(clip)}">
          ${clip.value}
        </div>
      </div>
    </foreignObject>
  </svg>`;
}
