import React, { useCallback } from 'react';
import { VideoClip } from '../../../../../common/types/clip';
import { EffectType, FitType } from '../../../../../common/types/player';
import EffectSelector from '../../../../../common/components/EffectSelector';
import FitSelector from '../../../../../common/components/FitSelector';

type Props = {
  data: VideoClip;
  onChange(data: Partial<VideoClip>): void;
};

const VideoPanel: React.FC<Props> = ({ data, onChange }) => {
  const handleEffectChange = useCallback((value: EffectType) => {
    onChange({
      effect: value,
    });
  }, [onChange]);
  const handleFitChange = useCallback((value: FitType) => {
    onChange({
      positionInRender: {
        ...data.positionInRender,
        fitType: value,
        width: data.originalWidth,
        height: data.originalHeight,
      },
    });
  }, [data.originalHeight, data.originalWidth, data.positionInRender, onChange]);
  return (
    <div>
      <EffectSelector
        value={data.effect}
        onChange={handleEffectChange}
      />
      <FitSelector
        value={data.positionInRender.fitType}
        onChange={handleFitChange}
      />
    </div>
  );
};

export default VideoPanel;
