import { EffectType, FitType, TransitionType } from './player';
import { OverlayProps } from '../../player/svgs';
import { AnimationItem } from 'lottie-web';
import { ParsedFrame } from 'gifuct-js';

export enum ClipType {
  UNKNOWN,
  VIDEO,
  IMAGE,
  AUDIO,
  TRANSITION,
  SVG,
  LOTTIE,
  TEXT,
  GIF,
}

export interface Clip {
  id?: string;
  name: string;
  type: ClipType;
  /**  */
  positionInTimeline: {
    start: number;
    end: number;
    duration: number;
  };
  /**  */
  positionInRender: {
    fitType: FitType;
    rotate: number;
    left: number;
    bottom: number;
    width: number;
    height: number;
  },
  /**  */
  playRange: {
    start: number;
    end: number;
  },
  effect: EffectType;
  originalWidth: number;
  originalHeight: number;
  zIndex: number;
}

export interface VideoClip extends Clip {
  url: string;
  blobUrl: string;
  buffer: ArrayBuffer;
}

export interface ImageClip extends Clip {
  url: string;
  blobUrl: string;
}

export interface AudioClip extends Clip {
  url: string;
  buffer: ArrayBuffer;
}

export interface LottieClip extends Clip {
  lottie: AnimationItem;
}

export interface TextClip extends Clip {
  value: string;
  props: {
    fontColor: string;
    fontSize: number;
    fontFamily: string;
  };
}

export interface SvgClip extends Clip {
  func(props: OverlayProps): string; 
}

export interface GifClip extends Clip {
  url: string;
  frames: ParsedFrame[];
}

export interface TransitionClip extends Clip {
  transition: TransitionType;
  fromId?: string;
  toId?: string;
}

export interface AudioMergeItem {
  /** player */
  delay: number;
  /**  */
  from: number;
  /**  */
  to: number;
  /**  */
  tempo: number;
  type: ClipType;
  buffer: ArrayBuffer;
}
