import React, { useCallback, useContext } from 'react';
import { editorContext } from '../..';
import { Radius10Button, Radius99Button } from '../../../common/styleds';
import { formatMillisecond } from '../../../common/utils';
import BackForward from '../../../icons/BackForward';
import Cut from '../../../icons/Cut';
import Delete from '../../../icons/Delete';
import FastForward from '../../../icons/FastForward';
import PauseButton from '../../../icons/pauseButton';
import PlayButton from '../../../icons/PlayButton';
import Redo from '../../../icons/Redo';
import Undo from '../../../icons/Undo';
import { PlayerContext } from '../../type';
import { deleteSelectedClip, splitSelectedClip } from '../../util';
import { ClipOperators, PlayButtons, PlayerControl, Ratios, StyledToolBar, Time, TimeLineZoom } from './styled';

type Props = {
  playerContext: PlayerContext;
};

const ToolBar: React.FC<Props> = ({ playerContext }) => {
  const editorContextValue = useContext(editorContext);
  const { isPlaying, totalDuration, currentTime } = editorContextValue;
  const handlePlayingChange = useCallback(() => {
    playerContext.handleIsPlayingChange(!isPlaying);
  }, [playerContext, isPlaying]);
  const handleDelete = useCallback(() => {
    deleteSelectedClip(editorContextValue);
  }, [editorContextValue]);
  const handleSplitClip = useCallback(() => {
    splitSelectedClip(editorContextValue);
  }, [editorContextValue]);
  const handleSetToStart = useCallback(() => {
    playerContext.handleCurrentTimeChange(0);
  }, [playerContext]);
  const handleSetToEnd = useCallback(() => {
    playerContext.handleCurrentTimeChange(totalDuration);
  }, [playerContext, totalDuration]);
  return (
    <StyledToolBar>

      <ClipOperators>
        <Radius10Button>
          <Undo />
        </Radius10Button>
        <Radius10Button>
          <Redo />
        </Radius10Button>
        <Radius10Button onClick={handleSplitClip}>
          <Cut />
        </Radius10Button>
        <Radius10Button onClick={handleDelete}>
          <Delete />
        </Radius10Button>
      </ClipOperators>

      <PlayerControl>
        <PlayButtons>
          <Radius10Button onClick={handleSetToStart}>
            <BackForward />
          </Radius10Button>
          <Radius10Button style={{ width: 60 }} onClick={handlePlayingChange}>
            {
              isPlaying ? <PauseButton /> : <PlayButton />
            }
          </Radius10Button>
          <Radius10Button onClick={handleSetToEnd}>
            <FastForward />
          </Radius10Button>
        </PlayButtons>

        <Time>
          <span style={{ color: '#367CE4' }}>
            {formatMillisecond(currentTime, true)}
          </span> / {formatMillisecond(totalDuration, true)}
        </Time>

        <Ratios>
          <Radius99Button style={{ marginRight: 16 }}>
            16:9
          </Radius99Button>
          <Radius99Button>
            9:16
          </Radius99Button>
        </Ratios>
      </PlayerControl>

      <TimeLineZoom>
        
      </TimeLineZoom>

    </StyledToolBar>
  );
};

export default ToolBar;
