import React, { useCallback, useContext } from 'react';
import { editorContext } from '../..';
import IndicatorBar from '../../../icons/IndicatorBar';
import { cursorPositionToTracksWrapperPosition, getTotalDuration, millisecondsToWidth, widthToMilliseconds } from '../../util';
import Track from './components/Track';
import { StyledIndicator, StyledTracks, StyledTracksWrapper } from './styled';

type Props = {
  onCurrentTimeChange(currentTime: number): void;
};

const Tracks: React.FC<Props> = ({ onCurrentTimeChange }) => {
  const { tracks, currentTime } = useContext(editorContext);
  const trackStyle: React.CSSProperties = tracks.length === 1 && tracks[0].length === 0 ? { height: 90 } : {};

  const handleCurrentTimeChange = useCallback((x: number, y: number, width: number) => {
    const totalDuration = getTotalDuration(tracks);
    const pos = cursorPositionToTracksWrapperPosition({ x, y });
    const newCurrentTime = widthToMilliseconds(Math.max(0, pos.x - width));
    onCurrentTimeChange(Math.min(newCurrentTime, totalDuration));
  }, [onCurrentTimeChange, tracks]);

  const handleTracksWrapperClick = useCallback((e: React.MouseEvent) => {
    handleCurrentTimeChange(e.pageX, e.pageY, 0);
  }, [handleCurrentTimeChange]);

  return (
    <>
      <StyledTracksWrapper className="tracks-wrapper" onClick={handleTracksWrapperClick}>
        <StyledTracks>
          {
            tracks.map((clips, index) => {
              return (
                <Track index={index} clips={clips} key={index} style={trackStyle} />
              );
            })
          }
        </StyledTracks>
      </StyledTracksWrapper>
      <StyledIndicator style={{ left: millisecondsToWidth(currentTime) + 20 }}>
        <IndicatorBar className="indicator-bar" />
      </StyledIndicator>
    </>
  );
};

export default Tracks;
