import React, { useCallback } from 'react';
import Logo from '../../../icons/Logo';
import Player from '../../../player';
import { exportAsVideo } from '../../../player/util';
import { ExportButton, StyledHeader } from './styled';

type Props = {
  player?: Player;
};

const Header: React.FC<Props> = ({ player }) => {
  const handleExportVideo = useCallback(() => {
    if (player) {
      exportAsVideo(player);
    }
  }, [player]);
  return (
    <StyledHeader>
      <Logo />
      <ExportButton onClick={handleExportVideo}></ExportButton>
    </StyledHeader>
  );
};

export default Header;
