import React, { useEffect, useRef } from 'react';
import { AudioClip } from '../../../../../../../common/types/clip';
import WaveSurfer from 'wavesurfer.js';
import { TRACK_HEIGHT } from '../../../../../../config';

type Props = {
  clip: AudioClip;
};

const AudioTrack: React.FC<Props> = ({ clip }) => {
  const containerRef = useRef<HTMLDivElement>(null);
  useEffect(() => {
    const container = containerRef.current;
    if (container) {
      const waveSurfer = WaveSurfer.create({
        container: container,
        waveColor: '#216fa3',
        progressColor: 'purple',
        fillParent: true,
        partialRender: true,
        interact: false,
        height: TRACK_HEIGHT,
      });
      waveSurfer.load(clip.url);
      return () => {
        waveSurfer.destroy();
      };
    }
  }, [clip.url]);
  return (
    <div style={{ width: '100%' }}>
      <div ref={containerRef} style={{ position: 'absolute', left: 0, right: 0, top: 0, bottom: 0 }}>
      </div>
    </div>
  );
};

export default AudioTrack;
