import React, { useCallback, useEffect, useRef } from 'react';
import { Clip } from '../../common/types/clip';
import Player from '../../player';
import { ClipBase } from '../../player/clips/base';
import { EditorContext, PlayerContext } from '../type';

/**
 * tracks
 */
const findDiff = (playerData: ClipBase[], newData: Clip[][]) => {
  const newClips: Clip[] = [];
  const deleteClips: string[] = [];
  const flatNewData = newData.flat(1);
  playerData.forEach((instance) => {
    const newClip = flatNewData.find((clip) => clip.id === instance.config.id);
    if (!newClip && instance.config.id) { // 
      deleteClips.push(instance.config.id);
    }
  });
  flatNewData.forEach((clip) => {
    const oldClip = playerData.find((item) => item.config.id === clip.id);
    if (!oldClip) {
      newClips.push(clip);
    }
  });
  return {
    newClips,
    deleteClips,
  };
}

/**
 * clips
 */
const tracksChanged = async (player: Player, newClips: Clip[], deleteClips: string[]) => {
  if (player) {
    const task: Promise<void>[] = [];
    newClips.forEach((clip) => {
      task.push(player?.addClip(clip));
    });
    deleteClips.forEach((id) => {
      task.push(player.removeClip(id));
    });
    await Promise.all(task).catch((error) => {
      alert(error.message);
    });
  }
};

export const usePlayer = (
  editorContextValue: EditorContext,
  containerRef: React.RefObject<HTMLDivElement>,
): PlayerContext => {
  const playerRef = useRef<Player>();
  const {
    tracks,
    totalDuration,
    setCurrentTime,
    isPlaying,
    setIsPlaying,
  } = editorContextValue;

  /**
   * 
   */
  useEffect(() => {
    if (!playerRef.current && containerRef.current) {
      playerRef.current = new Player({
        width: 768,
        height: 432,
        onCurrentTimeChange(currentTime: number) {
          setCurrentTime(currentTime);
        },
        onEnd() {
          setIsPlaying(false);
        },
      });
      playerRef.current.attachTo(containerRef.current);
    }
  }, [containerRef, setCurrentTime, setIsPlaying, tracks]);

  /**
   * 
   */
  useEffect(() => {
    if (playerRef.current && tracks.length) {
      const { newClips, deleteClips } = findDiff(playerRef.current.clips, tracks);
      tracksChanged(playerRef.current, newClips, deleteClips);
    }
  }, [tracks]);

  /**
   * /
   */
   const handleIsPlayingChange = useCallback(async (isPlaying: boolean) => {
    if (playerRef.current) {
      if (!isPlaying) {
        await playerRef.current.pause();
        setIsPlaying(false);
      } else {
        await playerRef.current.play();
        setIsPlaying(true);
      }
    }
  }, [setIsPlaying]);

  /**
   * 
   */
  const handleCurrentTimeChange = useCallback(async (currentTime: number) => {
    const player = playerRef.current;
    if (player) {
      player.pause();
      await player.setCurrentTime(currentTime);
      setIsPlaying(false);
      setCurrentTime(currentTime);
    }
  }, [setCurrentTime, setIsPlaying]);

  /**
   * 
   */
  useEffect(() => {
    if (playerRef.current && totalDuration >= 0) {
      playerRef.current.updateDuration();
    }
  }, [totalDuration]);

  return {
    player: playerRef.current,
    isPlaying,
    setIsPlaying,
    handleIsPlayingChange,
    handleCurrentTimeChange,
  };
};
