import React from 'react';
import { ASSET } from '../../../../../asset';
import { AssetCardContainer } from '../../../../../common/styleds';
import AssetCard from '../../../AssetCard';

const ImagePanel: React.FC = () => {
  return (
    <>
      {
        ASSET.images.map((item) => {
          return (
            <AssetCardContainer key={item.name}>
              <AssetCard data={item} />
            </AssetCardContainer>
          );
        })
      }
      {
        ASSET.gifs.map((item) => {
          return (
            <AssetCardContainer key={item.name}>
              <AssetCard data={item} />
            </AssetCardContainer>
          );
        })
      }
    </>
  );
};

export default ImagePanel;
