import { Select } from 'antd';
import React from 'react';
import { EffectType } from '../../types/player';

type Props = {
  value: EffectType;
  onChange(data: EffectType): void;
};

const EffectSelector: React.FC<Props> = ({
  value,
  onChange,
}) => {
  return (
    <div>
      <Select value={value} onSelect={onChange}>
        <Select.Option value={EffectType.NO_EFFECT}></Select.Option>
        <Select.Option value={EffectType.BLUR}></Select.Option>
        <Select.Option value={EffectType.GRAY}></Select.Option>
      </Select>
    </div>
  );
};

export default EffectSelector;
