import React, { useCallback, useContext, useEffect, useMemo, useState } from 'react';
import Moveable from 'react-moveable';
import { editorContext } from '../..';
import { ClipType, TextClip } from '../../../common/types/clip';
import { FitType } from '../../../common/types/player';
import { getClipRenderInfo } from '../../../player/util';
import { getPlayerContainerBounding, getTrackAndClipIndex } from '../../util';
import TextEditor from './components/TextEditor';
import { StyledTarget } from './styled';

const renderDirections = ['nw', 'ne', 'sw', 'se'];

const ResizeRotate: React.FC = () => {
  const { activeClipId, tracks } = useContext(editorContext);
  const [target, setTarget] = useState<HTMLDivElement | null>(null);
  const [targetStyle, setTargetStyle] = useState<React.CSSProperties>({});
  const [frame] = React.useState({ translate: [0, 0] });
  const activeClip = useMemo(() => {
    const { trackIndex, clipIndex } = getTrackAndClipIndex(tracks, activeClipId);
    if (trackIndex > -1 && clipIndex > -1) {
      return tracks[trackIndex][clipIndex];
    }
    return undefined;
  }, [activeClipId, tracks]);

  useEffect(() => {
    if (activeClip) {
      const { left: playerLeft, top, height: playerHeight, width: playerWidth } = getPlayerContainerBounding();
      const { width, height, left, bottom } = getClipRenderInfo(activeClip, playerWidth, playerHeight);
      setTargetStyle({
        width,
        height,
        left: playerLeft + left,
        top: top + playerHeight - bottom - height,
        transform: `rotate(${activeClip.positionInRender.rotate}deg)`,
      });
      setTarget(document.querySelector('.resize-rotate') as HTMLDivElement);
    } else {
      setTarget(null);
    }
  }, [activeClip]);

  const handleDragStart = useCallback(({ set }) => {
    set(frame.translate);
  }, [frame.translate]);

  const handleDrag = useCallback(({ target, delta, beforeTranslate }) => {
    frame.translate = beforeTranslate;
    target.style.transform = `translate(${beforeTranslate[0]}px, ${beforeTranslate[1]}px)`;
    if (activeClip) {
      Object.assign(activeClip, {
        positionInRender: {
          ...activeClip.positionInRender,
          fitType: FitType.CUSTOM,
          left: activeClip.positionInRender.left + delta[0],
          bottom: activeClip.positionInRender.bottom - delta[1],
        },
      });
    }
  }, [activeClip, frame]);

  const handleResizeStart = useCallback(({ setOrigin, dragStart }) => {
    setOrigin(['%', '%']);
    dragStart && dragStart.set(frame.translate);
  }, [frame.translate]);

  const handleResize = useCallback(({ target, width, height, delta, drag, direction }) => {
    const isDragBottom = direction[1] === 1;
    const isDragLeft = direction[0] === -1;
    const beforeTranslate = drag.beforeTranslate;
    frame.translate = beforeTranslate;
    if (activeClip) {
      Object.assign(activeClip, {
        positionInRender: {
          ...activeClip.positionInRender,
          fitType: FitType.CUSTOM,
          width: width,
          height: height,
          ...(isDragLeft ? { left: activeClip.positionInRender.left - delta[0] } : null),
          ...(isDragBottom ? { bottom: activeClip.positionInRender.bottom - delta[1] } : null),
        },
      });
    }
    delta[0] && (target!.style.width = `${width}px`);
    delta[1] && (target!.style.height = `${height}px`);
    target!.style.transform = `translate(${beforeTranslate[0]}px, ${beforeTranslate[1]}px)`;
  }, [activeClip, frame]);

  return (
    <>
      <StyledTarget className="resize-rotate" style={targetStyle}>
        {
          activeClip && activeClip.type === ClipType.TEXT ? <TextEditor clip={activeClip as TextClip} /> : null
        }
      </StyledTarget>
      <Moveable
        target={target}
        container={null}
        origin={false}

        renderDirections={renderDirections}

        /* Resize event edges */
        edge={false}

        /* draggable */
        draggable={true}
        throttleDrag={0}
        onDragStart={handleDragStart}
        onDrag={handleDrag}

        /* When resize or scale, keeps a ratio of the width, height. */
        keepRatio={true}

        /* resizable*/
        /* Only one of resizable, scalable, warpable can be used. */
        resizable={true}
        throttleResize={0}
        onResizeStart={handleResizeStart}
        onResize={handleResize}
      />
    </>
  );
};

export default ResizeRotate;
