import { v4 } from 'uuid';
import { Position, Range } from '../common/types';
import { Asset, AssetType, AudioAsset, TextAsset, TransitionAsset, VideoAsset } from '../common/types/asset';
import { AudioClip, Clip, ClipType, GifClip, ImageClip, TextClip, TransitionClip, VideoClip } from '../common/types/clip';
import { EffectType, FitType } from '../common/types/player';
import { SECOND_WIDTH, TRACK_WRAPPER_CLASS_NAME } from './config';
import { EditorContext } from './type';

const trackWrapperSelector = `.${TRACK_WRAPPER_CLASS_NAME}`;

/**
 * 
 */
export function cursorPositionToElementPosition(cursorPosition: Position, element: HTMLElement): Position {
  if (cursorPosition && element) {
    const rect = element.getBoundingClientRect();
    return {
      x: Math.max(0, cursorPosition.x - rect.x),
      y: Math.max(0, cursorPosition.y - rect.y),
    };
  }
  return { x: 0, y: 0 };
}

/**
 * 
 */
export function cursorPositionToTracksWrapperPosition(cursorPosition: Position) {
  const tracksWrapper = document.querySelector('.tracks-wrapper') as HTMLElement;
  if (cursorPosition && tracksWrapper) {
    const rect = tracksWrapper.getBoundingClientRect();
    const paddingLeft = parseInt(getComputedStyle(tracksWrapper).paddingLeft, 10);
    return {
      x: Math.max(0, cursorPosition.x - rect.x - paddingLeft),
      y: Math.max(0, cursorPosition.y - rect.y),
    };
  }
  return { x: 0, y: 0 };
}

/**
 * track index
 */
export function getTrackIndex(track?: HTMLElement | null) {
  if (track) {
    return Array.prototype.slice.call(track.parentElement?.querySelectorAll(trackWrapperSelector)).indexOf(track);
  }
  return -1;
}

export function getTrackWrapper(el: HTMLElement) {
  let parentElement = el?.parentElement;
  while (parentElement) {
    if (parentElement.classList.contains(TRACK_WRAPPER_CLASS_NAME)) {
      return parentElement;
    }
    parentElement = parentElement.parentElement;
  }
  return null;
  // return el?.closest(trackWrapperSelector) as HTMLElement;
}

/**
 * trackElementtrackIndexclipIndex
 */
export function getTrackAndClipIndex(data: Clip[][], id?: string) {
  for (let i = 0; i < data.length; i++) {
    for (let j = 0; j < data[i].length; j++) {
      if (data[i][j].id === id) {
        return { trackIndex: i, clipIndex: j };
      }
    }
  }
  return { trackIndex: -1, clipIndex: -1 };
}

export function needShowPrepareTrack(data: Clip[][]) {
  return !(data.length === 0 || (data.length === 1 && data[0].length === 0));
}

export function millisecondsToWidth(milliseconds?: number) {
  return (milliseconds || 0) / 1000 * SECOND_WIDTH;
}

export function widthToMilliseconds(width: number) {
  return Math.round(width / SECOND_WIDTH * 1000);
}

export function getClipInsertIndex(cursorX: number, data: Clip[]) {
  if (data.length) {
    for (let i = 0; i < data.length; i++) {
      const { start, end } = data[i].positionInTimeline;
      if (cursorX >= start && cursorX <= end) {
        return -1;
      }
      if (cursorX < start) {
        return i;
      }
    }
    if (cursorX > data[data.length - 1].positionInTimeline.end) {
      return data.length;
    }
  }
  return 0;
}

export function getAvailableStartAndEnd(prepareInsertIndex: number, data: Clip[]) {
  const pre = data[prepareInsertIndex - 1];
  const next = data[prepareInsertIndex];
  if (!pre && !next) {
    return { start: 0, end: 10e9 };
  }
  if (!pre && next) {
    return { start: 0, end: next.positionInTimeline.start };
  }
  if (pre && next) {
    return { start: pre.positionInTimeline.end, end: next.positionInTimeline.start };
  }
  if (pre && !next) {
    return { start: pre.positionInTimeline.end, end: 10e9 };
  }
  return { start: 0, end: 0 };
}

/**
 * 
 * @param availableRange 
 * @param insertRange 
 */
export function adjustStartAndEnd(availableRange: Range, insertRange: Range): Range {
  if (insertRange[0] >= availableRange[0] && insertRange[1] <= availableRange[1]) {
    return insertRange;
  }
  if (insertRange[0] >= availableRange[0] && insertRange[1] > availableRange[1]) { // 
    const diff = insertRange[1] - availableRange[1];
    return [
      Math.max(insertRange[0] - diff, availableRange[0]),
      Math.min(insertRange[1] - diff, availableRange[1]),
    ];
  }
  if (insertRange[0] < availableRange[0] && insertRange[1] <= availableRange[1]) { // 
    const diff = availableRange[0] - insertRange[0];
    return [
      Math.max(insertRange[0] + diff, availableRange[0]),
      Math.min(insertRange[1] + diff, availableRange[1]),
    ];
  }
  if (insertRange[0] <= availableRange[0] && insertRange[1] >= availableRange[1]) {
    return availableRange;
  }
  return insertRange;
}

/**
 * 
 */
export function getTotalDuration(data: Clip[][]) {
  let result = 0;
  data.forEach((track) => {
    if (track?.length) {
      const lastClip = track[track.length - 1];
      result = Math.max(result, lastClip.positionInTimeline.end);
    }
  });
  return result;
}

/**
 * clip
 */
export function isClipsNext(clip1?: Clip, clip2?: Clip) {
  if (clip1 && clip2) {
    return Math.abs(clip1.positionInTimeline.end - clip2.positionInTimeline.start) <= 10e-6;
  }
  return false;
}

export function canWithTransition(clipType: ClipType) {
  return [
    ClipType.VIDEO,
    ClipType.IMAGE,
    ClipType.GIF,
  ].includes(clipType);
}

export function isTransitionStart(tracks: Clip[][], clipId?: string) {
  const transitions = tracks.flat(1).filter((clip) => clip.type === ClipType.TRANSITION);
  return transitions.findIndex((transition) => (transition as TransitionClip).fromId === clipId) > -1;
}

export function isTransitionEnd(tracks: Clip[][], clipId?: string) {
  const transitions = tracks.flat(1).filter((clip) => clip.type === ClipType.TRANSITION);
  return transitions.findIndex((transition) => (transition as TransitionClip).toId === clipId) > -1;
}

export function getTransition(tracks: Clip[][], clipId?: string) {
  const transitions = tracks.flat(1).filter((clip) => clip.type === ClipType.TRANSITION);
  return transitions.findIndex((transition) => (transition as TransitionClip).fromId === clipId) > -1;
}

export function deleteSelectedClip(editorContextValue: EditorContext) {
  const { tracks, activeClipId, setTracks, setActiveClipId } = editorContextValue;
  const { trackIndex, clipIndex } = getTrackAndClipIndex(tracks, activeClipId);
  if (trackIndex > -1 && clipIndex > -1) {
    const clip = tracks[trackIndex][clipIndex];
    // transitiontoClipposition
    if (clip.type === ClipType.TRANSITION) {
      const fromClip = tracks[trackIndex][clipIndex - 1];
      const toClip = tracks[trackIndex][clipIndex + 1];
      const afterToClip = tracks[trackIndex][clipIndex + 2];
      const newRange = adjustStartAndEnd(
        [fromClip?.positionInTimeline?.end || 0, afterToClip?.positionInTimeline?.start || 10e9],
        [toClip.positionInTimeline.start + clip.positionInTimeline.duration / 2, toClip.positionInTimeline.end + clip.positionInTimeline.duration / 2]
      );
      Object.assign(toClip, {
        positionInTimeline: {
          start: newRange[0],
          end: newRange[1],
          duration: newRange[1] - newRange[0],
        },
      });
      tracks[trackIndex].splice(clipIndex, 1); // clip
    } else if (isTransitionStart(tracks, activeClipId)) {
      tracks[trackIndex].splice(clipIndex, 1); // clip
      tracks[trackIndex].splice(clipIndex, 1); // transition
    } else if (isTransitionEnd(tracks, activeClipId)) {
      tracks[trackIndex].splice(clipIndex, 1); // clip
      tracks[trackIndex].splice(clipIndex - 1, 1); // transition
    } else {
      tracks[trackIndex].splice(clipIndex, 1); // clip
    }
    setActiveClipId('');
    setTracks([...tracks]);
  }
}

export function isClipCanSplit(clipType: ClipType) {
  return [
    ClipType.AUDIO,
    ClipType.GIF,
    ClipType.IMAGE,
    ClipType.VIDEO,
  ].includes(clipType);
}

export function splitSelectedClip(editorContextValue: EditorContext) {
  const { tracks, activeClipId, setTracks, currentTime } = editorContextValue;
  const { trackIndex, clipIndex } = getTrackAndClipIndex(tracks, activeClipId);
  if (trackIndex > -1 && clipIndex > -1) {
    const clip = tracks[trackIndex][clipIndex];
    const canSplit = isClipCanSplit(clip.type);
    const { start, end } = clip.positionInTimeline;
    if (canSplit && start < currentTime && end > currentTime) {
      const splitPercent = (currentTime - start) / (end - start);
      const splitTime = Math.round((clip.playRange.end - clip.playRange.start) * splitPercent);
      // playRangepositionInTimelineplaybackRatebug
      tracks[trackIndex].splice(clipIndex + 1, 0, {
        ...clip,
        id: v4(),
        playRange: {
          start: splitTime,
          end: clip.playRange.end,
        },
        positionInTimeline: {
          start: currentTime,
          end,
          duration: end - currentTime,
        },
      });
      Object.assign(clip, {
        playRange: {
          start: clip.playRange.start,
          end: splitTime,
        },
        positionInTimeline: {
          start,
          end: currentTime,
          duration: currentTime - start,
        },
      });
      setTracks([...tracks]);
    }
  }
}

/**
 * onLoad
 */
 export function assetToClip(asset: Asset, start?: number, end?: number): Clip {
  const common = {
    name: asset.name,
    positionInTimeline: {
      start,
      end,
      duration: 0,
    },
    positionInRender: {
      fitType: FitType.CONTAIN,
      rotate: 0,
      left: 0,
      bottom: 0,
      width: 0,
      height: 0,
    },
    playRange: {
      start: 0,
      end: 0,
    },
    effect: EffectType.NO_EFFECT,
  };
  switch (asset.type) {
    case AssetType.VIDEO:
      return {
        ...common,
        type: ClipType.VIDEO,
        url: (asset as VideoAsset).url,
      } as VideoClip;
    case AssetType.IMAGE:
      return {
        ...common,
        type: ClipType.IMAGE,
        url: (asset as VideoAsset).url,
      } as ImageClip;
    case AssetType.AUDIO:
      return {
        ...common,
        type: ClipType.AUDIO,
        url: (asset as AudioAsset).url,
      } as AudioClip;
    case AssetType.TRANSITION:
      return {
        ...common,
        type: ClipType.TRANSITION,
        transition: (asset as TransitionAsset).transition,
      } as TransitionClip;
    case AssetType.GIF:
      return {
        ...common,
        positionInRender: {
          ...common.positionInRender,
          fitType: FitType.CUSTOM,
        },
        type: ClipType.GIF,
        url: (asset as VideoAsset).url,
        frames: [],
        originalWidth: 0,
        originalHeight: 0,
        zIndex: 0,
      } as GifClip;
    case AssetType.TEXT:
      return {
        ...common,
        positionInRender: {
          ...common.positionInRender,
          fitType: FitType.CUSTOM,
          left: 50,
          bottom: 50,
        },
        props: {
          fontColor: '#fff',
          fontSize: 16,
          fontFamily: 'Helvetica',
        },
        value: (asset as TextAsset).value,
        type: ClipType.TEXT,
        originalWidth: 0,
        originalHeight: 0,
        zIndex: 0,
      } as TextClip;
    default:
      return {} as Clip;
  }
}

export function getPlayerContainerBounding(): DOMRect {
  const playerContainer = document.querySelector('.player-container');
  if (playerContainer) {
    return playerContainer.getBoundingClientRect();
  }
  return {
    width: 0,
    height: 0,
    x: 0,
    y: 0,
    left: 0,
    right: 0,
    top: 0,
    bottom: 0,
    toJSON: () => {},
  };
}
