import React, { useCallback, useContext } from 'react';
import { editorContext } from '../../../..';
import { Clip, ClipType } from '../../../../../common/types/clip';
import { TRACK_WRAPPER_CLASS_NAME } from '../../../../config';
import { canWithTransition, getTrackAndClipIndex, getTrackIndex, isClipsNext, millisecondsToWidth, needShowPrepareTrack } from '../../../../util';
import TrackElement from '../TrackElement';
import { StyledTrackElement } from '../TrackElement/styled';
import { StyledNewPrepareTrack, StyledTrack, StyledTrackWrapper } from './styled';

type Props = {
  clips: Clip[];
  index: number;
  style?: React.CSSProperties;
};

const Track: React.FC<Props> = ({ clips, style, index }) => {
  const { hoverTrack, tracks, setTracks, newTrackIndex, draggingClip } = useContext(editorContext);
  const hoverIndex = getTrackIndex(hoverTrack);
  const handleUpdate = useCallback((oldData: Clip, newData: Partial<Clip>, newTrackIndex: number, newClipIndex: number, isNewTrack: boolean) => {
    // 
    const { trackIndex, clipIndex } = getTrackAndClipIndex(tracks, oldData.id);
    if (trackIndex > -1 && clipIndex > -1) {
      // clip
      const oldData = tracks[trackIndex].splice(clipIndex, 1)[0];
      Object.assign(oldData, newData);
      if (isNewTrack) {
        tracks.splice(newTrackIndex, 0, [oldData]);
      } else {
        tracks[newTrackIndex].splice(newClipIndex, 0, oldData);
      }
      setTracks([...tracks]);
    }
  }, [setTracks, tracks]);
  const showPrepareTrack = needShowPrepareTrack(tracks);
  const isDraggingTransition = draggingClip?.type === ClipType.TRANSITION;
  return (
    <>
      {
        showPrepareTrack && newTrackIndex === index ? <StyledNewPrepareTrack /> : null
      }
      <StyledTrackWrapper style={style} className={TRACK_WRAPPER_CLASS_NAME}>
        <StyledTrack
          style={{
            ...(clips.length ? { border: 0, background: '#121314' } : null),
            ...(hoverIndex === index && newTrackIndex === -1 ? { background: 'rgb(18 19 20 / 50%)' } : null)
          }}
        >
          {
            clips.map((clip, index) => {
              const nextClip = clips[index + 1];
              let showTransitionPrepareInsert = false;
              if (isDraggingTransition && isClipsNext(clip, nextClip)) {
                if (canWithTransition(clip?.type) && canWithTransition(nextClip?.type)) {
                  showTransitionPrepareInsert = true;
                }
              }
              return (
                <React.Fragment key={clip.id}>
                  <TrackElement
                    clip={clip}
                    onUpdate={(data: Partial<Clip>, newTrackIndex: number, newClipIndex: number, isNewTrack: boolean) => {
                      handleUpdate(clip, data, newTrackIndex, newClipIndex, isNewTrack);
                    }}
                  />
                  {
                    showTransitionPrepareInsert && draggingClip ? <StyledTrackElement
                      className="transition-insert"
                      data-from={clip.id}
                      data-to={nextClip.id}
                      style={{
                        width: millisecondsToWidth(draggingClip.positionInTimeline.duration),
                        left: millisecondsToWidth(clip.positionInTimeline.end - draggingClip.positionInTimeline.duration / 2),
                        zIndex: 999,
                        opacity: 0.5,
                        border: '2px solid #fff',
                      }}
                    /> : null
                  }
                </React.Fragment>
              );
            })
          }
        </StyledTrack>
      </StyledTrackWrapper>
      {
        showPrepareTrack && newTrackIndex === index + 1 ? <StyledNewPrepareTrack /> : null
      }
    </>
  );
};

export default Track;
