import { Select } from 'antd';
import React from 'react';
import { FitType } from '../../types/player';

type Props = {
  value: FitType;
  onChange(data: FitType): void;
};

const FitSelector: React.FC<Props> = ({
  value,
  onChange,
}) => {
  return (
    <div>
      <Select value={value} onSelect={onChange}>
        <Select.Option value={FitType.CUSTOM}></Select.Option>
        <Select.Option value={FitType.CONTAIN}>contain</Select.Option>
        <Select.Option value={FitType.COVER}>cover</Select.Option>
        <Select.Option value={FitType.FILL}>fill</Select.Option>
      </Select>
    </div>
  );
};

export default FitSelector;
